/* Reset styles */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  /* Global styles */
  body {
    font-family: Arial, sans-serif;
    background-color: #fff;
    color: #333;
    line-height: 1.5;
    font-size: 16px;
    display: flex;
    justify-content: center;
  }